export const Order = {
    namespaced: true,
    state: () => ({
        //   
        resPayment: 0,
    }),
    mutations: {
        paymentChange(state, {payment}){
            state.resPayment = payment;
        }
    },
    actions: {
        paymentAction(context, payload){
            const {payment} = payload;
            context.commit('paymentChange', {payment});
        }
    }
}